.ias-bg {
  background: #15142b;
  color: #fff;
  padding-top: 60px;
  padding-bottom: 60px;
}
.ias-label {
  color: #fff;
  font-family: "Urbanist";
  font-size: 12px;
  font-weight: 400;
  line-height: normal;
  text-transform: uppercase;
}

.ias-title {
  color: #fff;
  font-family: "Urbanist";
  font-size: 40px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
.contribution-banner {
  position: relative;
  overflow: hidden;
  border-radius: 24px;
  background: linear-gradient(130deg, #492fb2 -1.74%, #adadad 108.02%);
}
.contribution-content {
  padding: 3.5rem;
  h2 {
    color: #fff;
    font-family: Urbanist;
    font-size: 36px;
    font-style: normal;
    font-weight: 500;
    line-height: 42px; /* 116.667% */
  }
  p {
    color: #fff;
    font-family: Urbanist;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px; /* 150% */
  }
}

.nouser{
  p{
      color: #FFF;
font-family: Urbanist;
font-size: 18px;
font-style: normal;
font-weight: 400;
line-height: 150%; /* 27px */
  }
}
.makebg {
  position: absolute;
  bottom: 0;
  right: 0;
  height: 100%;
  z-index: 1;
  object-fit: cover;
}
.makeimage {
  position: relative;
  z-index: 2;
}
.badge {
  border-radius: 8px;
  background: #8c7fc3;
  display: inline-flex;
  gap: 3px;
  align-items: center;
  padding: 0 6px;
  margin: 0 4px;
  color: #fff;
  font-family: Urbanist;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}
.make-btn {
  border-radius: 8px;
  background: #fff;
  padding: 8px 12px;
  color: #063787;
  text-align: center;
  font-family: Urbanist;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 16px; /* 114.286% */
  letter-spacing: var(--Letter-Spacing-3, 0.5px);
}

.event-card {
  border-radius: 18px;
  border: 1px solid rgba(101, 130, 255, 0.18);
  transition: all 0.3s ease;
  height: 100%;
}
.event1 {
  background: linear-gradient(90deg, #1b112b 0%, #2f2a38 100%);
}
.event2 {
  background: linear-gradient(-90deg, #1b112b 0%, #2f2a38 100%);
}
.event-card:hover {
  transform: translateY(-5px);
}
.event-month {
  color: #fff;
  font-family: Urbanist;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 100%;
}
.event-day {
  font-family: Urbanist;
  font-size: 155px;
  font-style: normal;
  font-weight: 500;
  line-height: 80%;
  opacity: 0.2;
  background: linear-gradient(
    331deg,
    rgba(153, 153, 153, 0) -3.74%,
    rgba(255, 255, 255, 0.69) 89.74%
    );
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.event-details {
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  justify-content: space-between;
  h5 {
    color: #fff;
    font-family: Urbanist;
    font-size: 22px;
    font-style: normal;
    font-weight: 500;
    line-height: 102.5%; /* 22.55px */
  }
  p {
    color: #fff;
    font-family: Urbanist;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 162%; /* 25.92px */
    opacity: 0.5;
  }
}

@media only screen and (max-width: 767px) {
  .contribution-content {
    padding: 2rem;
  }
}
@media only screen and (max-width: 991px) {
  .makebg {
    display: none;
  }
}
@media only screen and (min-width: 992px) and (max-width: 1199px) {
.event-day{
  font-size: 132px;
}
}

